<template>
  <div style="padding-top: 0.7rem;">
  	<Detailshead></Detailshead>
  	<list :listArr="Details1"></list>
  	<size :lisize="Text" :lisize2="Text2" :lisize3="Text3"></size>
  	<Detaillist></Detaillist>
  </div>
</template>
<script lang="babel">
import Detailshead from "./common/Detailshead";
import Detaillist from "./common/Detaillist";
import list from "./common/Switchthebar";
import size from "./common/Detaisize";
export default {
	data(){//
		return{
			Details1:[],
			youth:[],
			Text:{},
			Text2:{},
			Text3:{}
		}	
	},
	methods:{//js
		
	},
	components:{//
	Detailshead,
	Detaillist,
	list,
	size
	},
	updated() {
		this.Text=this.youth[0]
		this.Text2=this.Details1[5]
		this.Text3=this.Details1[0]
	},
	beforeMount(){
		var _this = this;

			_this.$http({
				method: 'get', //ajax 
				url: "./static/json/youth.json", //()
				params: {} //data 
			}).then(function(data) {
				//data;success //sliderArr,sliderArr
				_this.youth = data.data; //_this.xxxArr=data.body;
			}, function(error) {
				//
				alert("");
			})
	},
	mounted(){//ajax
		var _this = this;

			_this.$http({
				method: 'get', //ajax 
				url: "./static/json/Details1.json", //()
				params: {} //data 
			}).then(function(data) {
				//data;success //sliderArr,sliderArr
				_this.Details1 = data.data; //_this.xxxArr=data.body;
			}, function(error) {
				//
				alert("");
			})
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>

</style>


