<template>
	<div>
		<headPlu></headPlu>
		<div style="overflow: hidden;height: 100%;position: relative;">
			<div class="youth":class="{tipAni:classTriBool}" :style="{transform: 'translateY('+moveY+'px)'}" @touchstart="jsScroll" @touchmove="jsScroll" @touchend="jsScroll">
				<div v-for="obj in youth" class="youthdiv youthdivjs1">
					<router-link :to="obj.toUrl">
						<div class=" youthdivjs2"><img :src="obj.imgUrl" />
							<div>
								<p class="youthp youthdivjs4">{{obj.text}}</p>
								<p class="youthp2">{{obj.digital}}</p>
								<div class="youdivhidden youthdivjs3">
									<p class="youthpleft">{{obj.Cross}}</p>
									<p class="youthpright"></p>
								</div>
							</div>
						</div>
					</router-link>
				</div>
			<span class="textrefresh tip">{{textrefresh}}</span>
			<span class="textload tip">{{textload}}</span>
			</div>
			
		</div>
		<cationfixed></cationfixed>
	</div>
</template>

<script lang="babel">
	import headPlu from "./common/headPlu";
	import cationfixed from "./common/cationfixed";

	export default {

		data() { //
			return {
				youth: [],
				pageNum:2,//
				longStep:50,//
				textrefresh:"",
				textload:"",
				startY:0,//
				endY:0,//
				moveY:0,//
				classTriBool:false,//
				boolRefresh:false,//
				boolloading:false,//
			}
		},
		methods: { //js
			jsScroll(e){
				var toucher=e.changedTouches[0];//
//				console.log(toucher)
				var _this=this;
				var scrollTop=document.body.scrollTop+document.documentElement.scrollTop;//
				var Height=document.documentElement.clientHeight;//	
				var actualHeight=document.documentElement.scrollHeight;//
				if(e.type=="touchstart"){
					
					_this.startY=toucher.pageY;
					_this.classTriBool=false;
														
				}
				else if(e.type=="touchmove"){
					
					
					if(scrollTop==0){//
						e.stopPropagation();//
						_this.moveY=(toucher.pageY-_this.startY)/3;
							if(_this.moveY>_this.longStep)
							{
								_this.boolRefresh=true;
								_this.textrefresh="";
							}
							else{
								
								_this.textrefresh="";
							}
						}
							else if(scrollTop>=actualHeight-Height && scrollTop!=0 && toucher.pageY<
							_this.startY){
								e.stopPropagation();//
							_this.moveY=(toucher.pageY-_this.startY)/3;
							if(-_this.moveY>_this.longStep)
							{
								_this.boolloading=true;
								_this.textload="";
							}
							else{
								
								_this.textload="";
							}
//								console.log(scrollTop)
							}
							
					
					
				}
				else if(e.type=="touchend"){
					_this.endY=toucher.pageY;
					_this.classTriBool=true;
					_this.moveY=0;
					//
					
					if(_this.boolRefresh)
					{//
						_this.classTriBool=false;
						_this.$http({
							method:"get",
							url:"./static/json/youth.json",
							params:{pageNum:1}
						}).then(function(data){
//							console.log(_this.$parent.listData)
							_this.youth=data.body;
							_this.textrefresh=""
							
							
						},function(err){
							_this.textrefresh=""
							alert("");
						})
					}
					if(_this.boolloading){
						_this.boolloading=false;
						_this.$http({
							method:"get",
							url:"./static/json/youth.json",
							params:{pageNum:_this.pageNum}
						}).then(function(data){
							_this.pageNum++;
//							console.log(_this.youth,data.body)
							_this.youth=_this.youth.concat(data.body);
							_this.textload="";
							
							
						},function(err){
							_this.textload="";
							alert("");
						})
						
					}
				}
				
			}
		},
		components: { //
			headPlu,
			cationfixed
		},
		mounted() { //ajax
			var _this = this;
			_this.$http({
				method: 'get', //ajax 
				url: "./static/json/youth.json", //()
				params: {} //data 
			}).then(function(data) {
				//data;success //sliderArr,sliderArr
				_this.youth = data.data; //_this.xxxArr=data.body;
			}, function(error) {
				//
				alert("");
			})
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss'>
	.youthdiv {
		width: 46.7%;
		margin: 0.1rem;
	}
	
	.youth {
		display: flex;
		flex-wrap: wrap;
		margin: 0rem 0.1rem 0 0.1rem;
		padding-top: 1.39rem;
	}
	
	.youthp {
		margin-top: 0.11rem;
		line-height: 0.19rem;
		padding-left: 0.08rem;
		font-size: 14px;
	}
	.youthp2{
		line-height: 0.19rem;
		padding-left: 0.08rem;
		height: .19rem;
		font-size: 14px;
		padding-top: 0.1rem;
		color: #ff0000;
		font-weight: 600;
	}
	.youthpleft{
		float: left;
		color: #cfdaed;
		line-height: 0.18rem;
		font-size: 14px;
		padding-left: 0.08rem;
		padding-top: 0.1rem;
	}
	.youthpright{
		float: right;
		color: #ff0000;
		line-height: 18px;
		border-radius: 0.05rem;
		font-size: 14px;
		padding: 0 0.03rem 0 0.03rem;
		border: 1px #ff0000 solid;
	}
	.youdivhidden{
		overflow: hidden;
	}
	.youthdiv-modify{
		height: 1.9rem;
		width: 100%;
		margin: 0.1rem;
		display: flex;
		position: relative;
		z-index: 1;
		& img{height: 100%;}
	}
	.youthp-modify{
	height: 1.8rem;
    margin: 0.1rem;
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    
	}
	.div-modify{
		display: flex;height: 100%;
		& img{
			    width: 1.3rem;
    			height: 1.8rem;
		}
	}
	.youthpp-modify{
	margin-top: 0.11rem;
    line-height: 0.19rem;
    padding-left: 0.08rem;
    font-size: 14px;
    width: 4.6rem;
	}
	.youdivhidden-modify{
	 overflow: hidden;
    position: absolute;
    bottom: 0;
    width: 70%;
	}
	.tip{position: absolute;left: 0;width: 100%;line-height: 40px;text-align: center;font-size: 14px ;}
	.textrefresh{top:  .82rem;}
	.textload{bottom: -0.61rem;}
</style>


