export default{
	fontFun:function(width){
	//width
	//
		var winWidth=document.documentElement.clientWidth;//
		var htmlNode=document.documentElement;//html
		if(winWidth>=width){
			htmlNode.style.fontSize="734.6%";
		}
		else{
			htmlNode.style.fontSize=winWidth/width*625+"%";
		}
	},
	init:function(){/**/
		var obj=this;
		obj.fontFun(640);//fontFun
		window.onresize=function(){//
			obj.fontFun(640);
		}
	}
};


